export const PERSONALISE_CHECKLIST_ACTION_TYPES = {

	GET_ALL_CHECKLIST_ITEMS: "GET_ALL_CHECKLIST_ITEMS",
	INCREMENT_CHECKLIST_QUANTITY: "INCREMENT_CHECKLIST_QUANTITY",
	DECREMENT_CHECKLIST_QUANTITY: "DECREMENT_CHECKLIST_QUANTITY",
	CHECKLIST_ADDED: "CHECKLIST_ADDED",
}

export const incrementCheckListQuantity = (id, quantity) => {
	return {
		type: PERSONALISE_CHECKLIST_ACTION_TYPES.INCREMENT_CHECKLIST_QUANTITY,
		payload: { id, quantity }
	}
}

export const decrementCheckListQuantity = (id, quantity) => {
	return {
		type: PERSONALISE_CHECKLIST_ACTION_TYPES.DECREMENT_CHECKLIST_QUANTITY,
		payload: { id, quantity }
	}
}


export const setPersonaliseCheckListItems = params => {
	return {
		type: PERSONALISE_CHECKLIST_ACTION_TYPES.GET_ALL_CHECKLIST_ITEMS,
		payload: params
	}
}

export const setIsActiveCheckList = (id, isAdded) => {
	return {
		type: PERSONALISE_CHECKLIST_ACTION_TYPES.CHECKLIST_ADDED,
		payload: { id, isAdded }
	}
}